<template><div class="markdown-body"><h1>小吃吧</h1>
<p><code v-pre="">mu-snackbar</code> 是一种针对操作的轻量级反馈机制，常以一个小的弹出框的形式。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-row&gt;\n    &lt;mu-col class=\&quot;demo-snackbar-radio\&quot; :key=\&quot;p\&quot; v-for=\&quot;p in positions\&quot; span=\&quot;6\&quot; md=\&quot;4\&quot;&gt;\n      &lt;mu-radio v-model=\&quot;normal.position\&quot; :value=\&quot;p\&quot; :label=\&quot;p\&quot;&gt;&lt;/mu-radio&gt;\n    &lt;/mu-col&gt;\n  &lt;/mu-row&gt;\n  &lt;mu-flex wrap=\&quot;wrap\&quot;&gt;\n    &lt;mu-text-field v-model=\&quot;normal.message\&quot; label=\&quot;Message\&quot; label-float&gt;&lt;/mu-text-field&gt;\n    &lt;mu-text-field v-model.number=\&quot;normal.timeout\&quot; label=\&quot;Timeout\&quot; type=\&quot;number\&quot; label-float&gt;&lt;/mu-text-field&gt;\n  &lt;/mu-flex&gt;\n  &lt;mu-button full-width color=\&quot;primary\&quot; @click=\&quot;openNormalSnackbar\&quot;&gt;show Snackbar&lt;/mu-button&gt;\n  &lt;mu-snackbar :position=\&quot;normal.position\&quot; :open.sync=\&quot;normal.open\&quot;&gt;\n    {{normal.message}}\n    &lt;mu-button flat slot=\&quot;action\&quot; color=\&quot;secondary\&quot; @click=\&quot;normal.open = false\&quot;&gt;Close&lt;/mu-button&gt;\n  &lt;/mu-snackbar&gt;\n&lt;/mu-container&gt;\n\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      positions: ['top-start', 'top', 'top-end', 'bottom-start', 'bottom', 'bottom-end'],\n      normal: {\n        position: 'bottom',\n        message: 'Hello World, Snackbar !',\n        open: false,\n        timeout: 3000\n      }\n    };\n  },\n  methods: {\n    openNormalSnackbar () {\n      if (this.normal.timer) clearTimeout(this.normal.timer);\n      this.normal.open = true;\n      this.normal.timer = setTimeout(() =&gt; {\n        this.normal.open = false;\n      }, this.normal.timeout);\n    }\n  }\n};\n&quot;,&quot;style&quot;:&quot;\n.demo-snackbar-radio {\n  margin: 8px 0;\n}\n&quot;}">
                    <div class="source" slot="source"><mu-container>
  <mu-row>
    <mu-col class="demo-snackbar-radio" :key="p" v-for="p in positions" span="6" md="4">
      <mu-radio v-model="normal.position" :value="p" :label="p"></mu-radio>
    </mu-col>
  </mu-row>
  <mu-flex wrap="wrap">
    <mu-text-field v-model="normal.message" label="Message" label-float=""></mu-text-field>
    <mu-text-field v-model.number="normal.timeout" label="Timeout" type="number" label-float=""></mu-text-field>
  </mu-flex>
  <mu-button full-width="" color="primary" @click="openNormalSnackbar">show Snackbar</mu-button>
  <mu-snackbar :position="normal.position" :open.sync="normal.open">
    {{normal.message}}
    <mu-button flat="" slot="action" color="secondary" @click="normal.open = false">Close</mu-button>
  </mu-snackbar>
</mu-container>



</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-row</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-col</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-snackbar-radio"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"p"</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"p in positions"</span> <span class="hljs-attr">span</span>=<span class="hljs-string">"6"</span> <span class="hljs-attr">md</span>=<span class="hljs-string">"4"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"normal.position"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"p"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"p"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-col</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-row</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">wrap</span>=<span class="hljs-string">"wrap"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"normal.message"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"Message"</span> <span class="hljs-attr">label-float</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">v-model.number</span>=<span class="hljs-string">"normal.timeout"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"Timeout"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"number"</span> <span class="hljs-attr">label-float</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">full-width</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openNormalSnackbar"</span>&gt;</span>show Snackbar<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-snackbar</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"normal.position"</span> <span class="hljs-attr">:open.sync</span>=<span class="hljs-string">"normal.open"</span>&gt;</span>
    {{normal.message}}
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">flat</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"action"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"secondary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"normal.open = false"</span>&gt;</span>Close<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-snackbar</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">positions</span>: [<span class="hljs-string">'top-start'</span>, <span class="hljs-string">'top'</span>, <span class="hljs-string">'top-end'</span>, <span class="hljs-string">'bottom-start'</span>, <span class="hljs-string">'bottom'</span>, <span class="hljs-string">'bottom-end'</span>],
      <span class="hljs-attr">normal</span>: {
        <span class="hljs-attr">position</span>: <span class="hljs-string">'bottom'</span>,
        <span class="hljs-attr">message</span>: <span class="hljs-string">'Hello World, Snackbar !'</span>,
        <span class="hljs-attr">open</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">timeout</span>: <span class="hljs-number">3000</span>
      }
    };
  },
  <span class="hljs-attr">methods</span>: {
    openNormalSnackbar () {
      <span class="hljs-keyword">if</span> (<span class="hljs-keyword">this</span>.normal.timer) clearTimeout(<span class="hljs-keyword">this</span>.normal.timer);
      <span class="hljs-keyword">this</span>.normal.open = <span class="hljs-literal">true</span>;
      <span class="hljs-keyword">this</span>.normal.timer = setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
        <span class="hljs-keyword">this</span>.normal.open = <span class="hljs-literal">false</span>;
      }, <span class="hljs-keyword">this</span>.normal.timeout);
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.demo-snackbar-radio</span> {
  <span class="hljs-attribute">margin</span>: <span class="hljs-number">8px</span> <span class="hljs-number">0</span>;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>

</code></pre>
</div></demo-block>
<h2 id="bu-tong-yan-se-he-tu-biao"><a class="header-anchor" href="#bu-tong-yan-se-he-tu-biao" aria-hidden="true">¶</a> 不同颜色和图标</h2>
<p>可以选择不同的颜色的 <code v-pre="">mu-snackbar</code>，以便于展示不同类型的提示信息。</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-row&gt;\n    &lt;mu-col class=\&quot;demo-snackbar-radio\&quot; :key=\&quot;c\&quot; v-for=\&quot;c in colors\&quot; span=\&quot;6\&quot; md=\&quot;3\&quot;&gt;\n      &lt;mu-radio v-model=\&quot;color.color\&quot; :color=\&quot;color.color\&quot; :value=\&quot;c\&quot; :label=\&quot;c\&quot;&gt;&lt;/mu-radio&gt;\n    &lt;/mu-col&gt;\n  &lt;/mu-row&gt;\n  &lt;mu-flex wrap=\&quot;wrap\&quot;&gt;\n    &lt;mu-text-field v-model=\&quot;color.message\&quot; label=\&quot;Message\&quot; label-float&gt;&lt;/mu-text-field&gt;\n    &lt;mu-text-field v-model.number=\&quot;color.timeout\&quot; label=\&quot;Timeout\&quot; type=\&quot;number\&quot; label-float&gt;&lt;/mu-text-field&gt;\n  &lt;/mu-flex&gt;\n  &lt;mu-button full-width color=\&quot;primary\&quot; @click=\&quot;openColorSnackbar\&quot;&gt;show Snackbar&lt;/mu-button&gt;\n  &lt;mu-snackbar :color=\&quot;color.color\&quot; :open.sync=\&quot;color.open\&quot;&gt;\n    &lt;mu-icon left :value=\&quot;icon\&quot;&gt;&lt;/mu-icon&gt;\n    {{color.message}}\n    &lt;mu-button flat slot=\&quot;action\&quot; color=\&quot;#fff\&quot; @click=\&quot;color.open = false\&quot;&gt;Close&lt;/mu-button&gt;\n  &lt;/mu-snackbar&gt;\n&lt;/mu-container&gt;\n\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      colors: ['success', 'info', 'error', 'warning'],\n      color: {\n        color: 'success',\n        message: 'Hello World, Snackbar !',\n        open: false,\n        timeout: 3000\n      }\n    };\n  },\n  computed: {\n    icon () {\n      return {\n        success: 'check_circle',\n        info: 'info',\n        warning: 'priority_high',\n        error: 'warning'\n      }[this.color.color]\n    }\n  },\n  methods: {\n    openColorSnackbar () {\n      if (this.color.timer) clearTimeout(this.color.timer);\n      this.color.open = true;\n      this.color.timer = setTimeout(() =&gt; {\n        this.color.open = false;\n      }, this.color.timeout);\n    }\n  }\n};\n&quot;,&quot;style&quot;:&quot;\n.demo-snackbar-radio {\n  margin: 8px 0;\n}\n&quot;}">
                    <div class="source" slot="source"><mu-container>
  <mu-row>
    <mu-col class="demo-snackbar-radio" :key="c" v-for="c in colors" span="6" md="3">
      <mu-radio v-model="color.color" :color="color.color" :value="c" :label="c"></mu-radio>
    </mu-col>
  </mu-row>
  <mu-flex wrap="wrap">
    <mu-text-field v-model="color.message" label="Message" label-float=""></mu-text-field>
    <mu-text-field v-model.number="color.timeout" label="Timeout" type="number" label-float=""></mu-text-field>
  </mu-flex>
  <mu-button full-width="" color="primary" @click="openColorSnackbar">show Snackbar</mu-button>
  <mu-snackbar :color="color.color" :open.sync="color.open">
    <mu-icon left="" :value="icon"></mu-icon>
    {{color.message}}
    <mu-button flat="" slot="action" color="#fff" @click="color.open = false">Close</mu-button>
  </mu-snackbar>
</mu-container>



</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-row</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-col</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-snackbar-radio"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"c"</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"c in colors"</span> <span class="hljs-attr">span</span>=<span class="hljs-string">"6"</span> <span class="hljs-attr">md</span>=<span class="hljs-string">"3"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"color.color"</span> <span class="hljs-attr">:color</span>=<span class="hljs-string">"color.color"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"c"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"c"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-col</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-row</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-flex</span> <span class="hljs-attr">wrap</span>=<span class="hljs-string">"wrap"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"color.message"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"Message"</span> <span class="hljs-attr">label-float</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-text-field</span> <span class="hljs-attr">v-model.number</span>=<span class="hljs-string">"color.timeout"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"Timeout"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"number"</span> <span class="hljs-attr">label-float</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-text-field</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">full-width</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openColorSnackbar"</span>&gt;</span>show Snackbar<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-snackbar</span> <span class="hljs-attr">:color</span>=<span class="hljs-string">"color.color"</span> <span class="hljs-attr">:open.sync</span>=<span class="hljs-string">"color.open"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">left</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"icon"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span>
    {{color.message}}
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">flat</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"action"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"#fff"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"color.open = false"</span>&gt;</span>Close<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-snackbar</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">colors</span>: [<span class="hljs-string">'success'</span>, <span class="hljs-string">'info'</span>, <span class="hljs-string">'error'</span>, <span class="hljs-string">'warning'</span>],
      <span class="hljs-attr">color</span>: {
        <span class="hljs-attr">color</span>: <span class="hljs-string">'success'</span>,
        <span class="hljs-attr">message</span>: <span class="hljs-string">'Hello World, Snackbar !'</span>,
        <span class="hljs-attr">open</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">timeout</span>: <span class="hljs-number">3000</span>
      }
    };
  },
  <span class="hljs-attr">computed</span>: {
    icon () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">success</span>: <span class="hljs-string">'check_circle'</span>,
        <span class="hljs-attr">info</span>: <span class="hljs-string">'info'</span>,
        <span class="hljs-attr">warning</span>: <span class="hljs-string">'priority_high'</span>,
        <span class="hljs-attr">error</span>: <span class="hljs-string">'warning'</span>
      }[<span class="hljs-keyword">this</span>.color.color]
    }
  },
  <span class="hljs-attr">methods</span>: {
    openColorSnackbar () {
      <span class="hljs-keyword">if</span> (<span class="hljs-keyword">this</span>.color.timer) clearTimeout(<span class="hljs-keyword">this</span>.color.timer);
      <span class="hljs-keyword">this</span>.color.open = <span class="hljs-literal">true</span>;
      <span class="hljs-keyword">this</span>.color.timer = setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
        <span class="hljs-keyword">this</span>.color.open = <span class="hljs-literal">false</span>;
      }, <span class="hljs-keyword">this</span>.color.timeout);
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.demo-snackbar-radio</span> {
  <span class="hljs-attribute">margin</span>: <span class="hljs-number">8px</span> <span class="hljs-number">0</span>;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>

</code></pre>
</div></demo-block>
<h2 id="snackbar-props"><a class="header-anchor" href="#snackbar-props" aria-hidden="true">¶</a> Snackbar Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>color</td>
<td>颜色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>text-color</td>
<td>文本颜色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>message</td>
<td>信息文本</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>position</td>
<td>弹出的位置</td>
<td>String</td>
<td>top-start / top / top-end / bottom-start / bottom / bottom-end</td>
<td>bottom</td>
</tr>
<tr>
<td>open</td>
<td>是否打开</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2 id="snackbar-slots"><a class="header-anchor" href="#snackbar-slots" aria-hidden="true">¶</a> Snackbar Slots</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>介绍</th>
</tr>
</thead>
<tbody>
<tr>
<td>action</td>
<td>放置操作按钮的插槽</td>
</tr>
<tr>
<td>default</td>
<td>放置文本信息和图标</td>
</tr>
</tbody>
</table>


</div class="markdown-body"></template>
<style>
.demo-snackbar-radio {
  margin: 8px 0;
}
</style>
<script>
export default {
  data () {
    return {
      positions: ['top-start', 'top', 'top-end', 'bottom-start', 'bottom', 'bottom-end'],
      colors: ['success', 'info', 'error', 'warning'],
      normal: {
        position: 'bottom',
        message: 'Hello World, Snackbar !',
        open: false,
        timeout: 3000
      },
      color: {
        color: 'success',
        message: 'Hello World, Snackbar !',
        open: false,
        timeout: 3000
      }
    };
  },
  computed: {
    icon () {
      return {
        success: 'check_circle',
        info: 'info',
        warning: 'priority_high',
        error: 'warning'
      }[this.color.color]
    }
  },
  methods: {
    openNormalSnackbar () {
      if (this.normal.timer) clearTimeout(this.normal.timer);
      this.normal.open = true;
      this.normal.timer = setTimeout(() => {
        this.normal.open = false;
      }, this.normal.timeout);
    },
    openColorSnackbar () {
      if (this.color.timer) clearTimeout(this.color.timer);
      this.color.open = true;
      this.color.timer = setTimeout(() => {
        this.color.open = false;
      }, this.color.timeout);
    }
  }
};
</script>


